body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.article-list {
    max-width: 100%;
    margin: 30px auto auto;
}
.pagination {
    text-align: center;
    margin-bottom: 50px;
}
.article-item {
    width: 500px;
    margin: 0 auto;
    /*background-color: #000;*/
    /*color: #fff;*/
    padding: 12px;
    color: #282c34;
    border-radius: 10px;
    margin-bottom: 10px;
}
.title-label a {
    color: #282c34;
    font-size: 15px;
    margin-bottom: 5px;
    text-decoration: none;
}
.title-label h1 {
    margin-top: 0;
    margin-bottom: 5px;
}
.title-label a:hover {
    color: #61dafb;
}
.date-label {
    font-size: 11px;
}
.content-hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
    width: 100px;
}


.pagination-area {
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
}
.pagination-button {
    width: 36px;
    height: 36px;
    background: white;
}
.article-root {
    width: 800px;
    margin: auto;
}
.content-header {
    padding: 10px 10px 0px 10px;
    margin-bottom: 0px;
}
.content-header-date {
    margin-top: 0px;

}
.header-root {
    height: 200px;
    background-color: #282c34;
    color:whitesmoke;
    display: inline-block;
    width: 100%;
}
#blog-title  p {
    font-size: 75px;
    vertical-align: middle;
}
.header-nav-root {
    height: 40px;
    background-color: #282c34;

}

.header-nav-content li {
    display: inline-block;
    color: whitesmoke;
    margin: 10px 5px 10px 5px;
}

.header-nav-content li:hover {
    color: #61dafb;
}
.text-center {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

div.test {
  border: 10px solid #282c34 !important;
}

.item-center {
  text-align: center;
}
