.article-item {
    width: 500px;
    margin: 0 auto;
    /*background-color: #000;*/
    /*color: #fff;*/
    padding: 12px;
    color: #282c34;
    border-radius: 10px;
    margin-bottom: 10px;
}
.title-label a {
    color: #282c34;
    font-size: 15px;
    margin-bottom: 5px;
    text-decoration: none;
}
.title-label h1 {
    margin-top: 0;
    margin-bottom: 5px;
}
.title-label a:hover {
    color: #61dafb;
}
.date-label {
    font-size: 11px;
}
.content-hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
    width: 100px;
}

